'use strict'

import { API } from '../modules/api'

export interface Store {
	id: string
	name: string
	card_img: string
	rating: number | string
	description?: string
	address?: string
	open_at?: string
	closed_at?: string
	city_id?: string
	tags?: string[]
	delivery_time?: string
}

export interface Item {
	id: string
	name: string
	description: string
	price: number
	card_img: string
	types_id?: string[]
}

export interface ItemType {
	id: string
	name: string
}

export interface StoreReview {
	comment: string
	created_at: string
	rating: number
	user_name: string
}

export interface City {
	id: string
	name: string
}

export interface Tag {
	id: string
	name: string
}

export interface GetStoresParams {
	limit?: number
	lastId?: string
	tagId?: string
	sorted?: string
	desc?: boolean
	search?: string
	category?: string
	cityID?: string
}

export interface StoresResponse {
	stores: Store[]
	total?: number
	hasMore?: boolean
	lastId?: string
}

export interface CartUpdate {
	items: { id: string; quantity: number }[]
}

export interface UpdateResponse {
	id: string
}

export interface CartItem {
	id: string
	name: string
	price: number
	quantity: number
	card_img: string
}

export interface Cart {
	id: string
	items: CartItem[]
}

export class StoreApi {
	/**
	 *   
	 * @param {GetStoresParams} params -  
	 * @returns {Promise<Store[]>} -  
	 */
	static async getStores(params: GetStoresParams = {}): Promise<Store[]> {
		const body = {
			limit: params.limit || 12,
			last_id: params.lastId || '',
			tag_id: params.tagId || '',
			sorted: params.sorted || '',
			desc: params.desc || false,
			search: params.search || '',
			category: params.category || '',
			cityID: params.cityID || '',
		}

		const response = await API.fetch('/stores', {
			method: 'POST',
			body: JSON.stringify(body),
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		const data = await response.json()

		if (Array.isArray(data)) {
			return data
		} else if (data && Array.isArray(data.stores)) {
			return data.stores
		} else {
			return []
		}
	}

	/**
	 *    ID
	 * @param {string} storeId - ID 
	 * @returns {Promise<Store | null>} - 
	 */
	static async getStoreById(storeId: string): Promise<Store | null> {
		const response = await API.fetch(`/stores/${storeId}`, {
			method: 'GET',
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		return await response.json()
	}

	/**
	 *    
	 * @param {string} storeId - ID 
	 * @returns {Promise<ItemType[]>} -   
	 */
	static async getStoreItemTypes(storeId: string): Promise<ItemType[]> {
		const response = await API.fetch(`/stores/${storeId}/item-types`, {
			method: 'GET',
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		return await response.json()
	}

	/**
	 *     
	 * @param {string} typeId - ID  
	 * @returns {Promise<Item[]>} -  
	 */
	static async getItemsByType(typeId: string): Promise<Item[]> {
		const response = await API.fetch(`/stores/${typeId}/items`, {
			method: 'GET',
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		return await response.json()
	}

	/**
	 *    
	 * @param {string} storeId - ID 
	 * @returns {Promise<Item[]>} -    
	 */
	static async getStoreItems(storeId: string): Promise<Item[]> {
		try {
			const response = await API.fetch(`/stores/${storeId}/items`, {
				method: 'GET',
			})

			if (!response.ok) {
				throw new Error(`HTTP error! status: ${response.status}`)
			}

			const data = await response.json()
			return Array.isArray(data) ? data : []
		} catch (error) {
			console.error('Error loading store items:', error)
			return []
		}
	}

	/**
	 *   
	 * @param {string} storeId - ID 
	 * @returns {Promise<StoreReview[]>} -  
	 */
	static async getStoreReviews(storeId: string): Promise<StoreReview[]> {
		const response = await API.fetch(`/stores/${storeId}/reviews`, {
			method: 'GET',
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		return await response.json()
	}

	/**
	 *   
	 * @returns {Promise<City[]>} -  
	 */
	static async getCities(): Promise<City[]> {
		const response = await API.fetch('/stores/cities', {
			method: 'GET',
			headers: {},
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		return await response.json()
	}

	/**
	 *   
	 * @returns {Promise<Tag[]>} -  
	 */
	static async getTags(): Promise<Tag[]> {
		const response = await API.fetch('/stores/tags', {
			method: 'GET',
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		return await response.json()
	}

	/**
	 *  
	 * @param {string} query -  
	 * @param {number} limit -  
	 * @returns {Promise<Store[]>} -  
	 */
	static async searchStores(
		query: string,
		limit: number = 12,
	): Promise<Store[]> {
		return this.getStores({ search: query, limit })
	}

	/**
	 *    
	 * @param {string} category - 
	 * @param {number} limit -  
	 * @returns {Promise<Store[]>} -  
	 */
	static async getStoresByCategory(
		category: string,
		limit: number = 12,
	): Promise<Store[]> {
		return this.getStores({ category, limit })
	}

	/**
	 *    
	 * @param {string} cityId - ID 
	 * @param {number} limit -  
	 * @returns {Promise<Store[]>} -  
	 */
	static async getStoresByCity(
		cityId: string,
		limit: number = 12,
	): Promise<Store[]> {
		return this.getStores({ cityID: cityId, limit })
	}

	/**
	 *    
	 * @param {string} tagId - ID 
	 * @param {number} limit -  
	 * @returns {Promise<Store[]>} -  
	 */
	static async getStoresByTag(
		tagId: string,
		limit: number = 12,
	): Promise<Store[]> {
		return this.getStores({ tagId, limit })
	}

	//  StoreApi.ts

	/**
	 *   
	 */
	static async getUserCart(userId: string): Promise<Cart[]> {
		const response = await API.fetch(`/users/${userId}/cart`, {
			method: 'GET',
		})
		if (!response.ok) {throw new Error(`HTTP ${response.status}`)}
		return await response.json()
	}

	/**
	 *  
	 */
	static async updateCart(
		cartId: string,
		cartUpdate: CartUpdate,
	): Promise<UpdateResponse> {
		const response = await API.fetch(`/carts/${cartId}`, {
			method: 'PUT',
			body: JSON.stringify(cartUpdate),
		})
		if (!response.ok) {throw new Error(`HTTP ${response.status}`)}
		return await response.json()
	}

	static async syncCity(userId: string, cityId: string): Promise<void> {
		await API.fetch(`/users/${userId}/city`, {
			method: 'POST',
			body: JSON.stringify({ city_id: cityId }),
		})
	}
}
