___CSS_LOADER_EXPORT___.push([module.id, `.product-card {
  background: var(--color-light);
  border-radius: var(--border-radius-large);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.product-card:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.product-card__image-wrapper {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.product-card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.product-card__content {
  padding: 16px;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.product-card__name {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--color-dark);
  line-height: 1.3;
}

.product-card__description {
  margin: 0;
  font-size: 0.9rem;
  color: #666;
  line-height: 1.4;
  flex: 1;
  display: -webkit-box;
  overflow: hidden;
}

.product-card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.product-card__price {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--color-dark);
}

.product-card__add-btn {
  width: 40px;
  height: 40px;
  border: none;
  background: var(--color-accent);
  color: var(--color-light);
  border-radius: 50%;
  font-size: 1.5rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s, transform 0.2s;
}

.product-card__add-btn:hover {
  background: var(--color-accent-hover);
}`, "",{"version":3,"sources":["webpack://./src/components/ProductCard/ProductCard.css"],"names":[],"mappings":"AAAA;EACE,8BAA8B;EAC9B,yCAAyC;EACzC,wCAAwC;EACxC,gBAAgB;EAChB,2CAA2C;EAC3C,YAAY;EACZ,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,0CAA0C;AAC5C;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,aAAa;EACb,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,OAAO;EACP,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,SAAS;EACT,iBAAiB;EACjB,gBAAgB;EAChB,wBAAwB;EACxB,gBAAgB;AAClB;;AAEA;EACE,SAAS;EACT,iBAAiB;EACjB,WAAW;EACX,gBAAgB;EAChB,OAAO;EACP,oBAAoB;EACpB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,wBAAwB;AAC1B;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,YAAY;EACZ,+BAA+B;EAC/B,yBAAyB;EACzB,kBAAkB;EAClB,iBAAiB;EACjB,gBAAgB;EAChB,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iDAAiD;AACnD;;AAEA;EACE,qCAAqC;AACvC","sourcesContent":[".product-card {\r\n  background: var(--color-light);\r\n  border-radius: var(--border-radius-large);\r\n  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);\r\n  overflow: hidden;\r\n  transition: transform 0.2s, box-shadow 0.2s;\r\n  height: 100%;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.product-card:hover {\r\n  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);\r\n}\r\n\r\n.product-card__image-wrapper {\r\n  position: relative;\r\n  width: 100%;\r\n  height: 200px;\r\n  overflow: hidden;\r\n}\r\n\r\n.product-card__image {\r\n  width: 100%;\r\n  height: 100%;\r\n  object-fit: cover;\r\n  display: block;\r\n}\r\n\r\n.product-card__content {\r\n  padding: 16px;\r\n  flex: 1;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 12px;\r\n}\r\n\r\n.product-card__name {\r\n  margin: 0;\r\n  font-size: 1.1rem;\r\n  font-weight: 600;\r\n  color: var(--color-dark);\r\n  line-height: 1.3;\r\n}\r\n\r\n.product-card__description {\r\n  margin: 0;\r\n  font-size: 0.9rem;\r\n  color: #666;\r\n  line-height: 1.4;\r\n  flex: 1;\r\n  display: -webkit-box;\r\n  overflow: hidden;\r\n}