___CSS_LOADER_EXPORT___.push([module.id, `.batch {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    justify-items: center;
    width: 100%;
    min-height: 0;
}

.stores-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;        
    padding: 0 10px;
    box-sizing: border-box;
}

.batch-header {
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
}

.batch-stats {
    font-size: 14px;
    color: var(--color-gray);
}

.batch-loading {
    text-align: center;
    padding: 40px;
    color: var(--color-gray);
}

.batch-load-more {
    display: block;
    margin: 20px auto;
    padding: 12px 24px;
    background: var(--color-accent);
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
}

.batch-load-more:hover {
    background: var(--color-accent-dark);
}

.batch-end {
    text-align: center;
    padding: 20px;
    color: var(--color-gray);
    font-style: italic;
}

.batch-error {
    background: var(--color-error-light);
    color: var(--color-error);
    padding: 16px;
    border-radius: 8px;
    margin-bottom: 20px;
    border: 1px solid var(--color-error);
}

.batch-retry-btn {
    background: var(--color-error);
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 8px;
}

.batch-empty {
    text-align: center;
    padding: 40px;
    color: var(--color-gray);
}`, "",{"version":3,"sources":["webpack://./src/components/Batch/Batch.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,eAAe;IACf,6BAA6B;IAC7B,qBAAqB;IACrB,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,SAAS;IACT,WAAW;IACX,iBAAiB;IACjB,cAAc;IACd,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,wBAAwB;AAC5B;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,wBAAwB;AAC5B;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,kBAAkB;IAClB,+BAA+B;IAC/B,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,wBAAwB;IACxB,kBAAkB;AACtB;;AAEA;IACI,oCAAoC;IACpC,yBAAyB;IACzB,aAAa;IACb,kBAAkB;IAClB,mBAAmB;IACnB,oCAAoC;AACxC;;AAEA;IACI,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,wBAAwB;AAC5B","sourcesContent":[".batch {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: space-evenly;\r\n    justify-items: center;\r\n    width: 100%;\r\n    min-height: 0;\r\n}\r\n\r\n.stores-grid {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: center;\r\n    gap: 20px;\r\n    width: 100%;\r\n    max-width: 1200px;\r\n    margin: 0 auto;        \r\n    padding: 0 10px;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.batch-header {\r\n    width: 100%;\r\n    text-align: center;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.batch-stats {\r\n    font-size: 14px;\r\n    color: var(--color-gray);\r\n}\r\n\r\n.batch-loading {\r\n    text-align: center;\r\n    padding: 40px;\r\n    color: var(--color-gray);\r\n}\r\n\r\n.batch-load-more {\r\n    display: block;\r\n    margin: 20px auto;\r\n    padding: 12px 24px;\r\n    background: var(--color-accent);\r\n    color: white;\r\n    border: none;\r\n    border-radius: 6px;\r\n    cursor: pointer;\r\n    font-size: 16px;\r\n}\r\n\r\n.batch-load-more:hover {\r\n    background: var(--color-accent-dark);\r\n}\r\n\r\n.batch-end {\r\n    text-align: center;\r\n    padding: 20px;\r\n  