___CSS_LOADER_EXPORT___.push([module.id, `.cart-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  background: rgba(255, 255, 255, 0.7);
  border-radius: var(--border-radius-medium);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.cart-item__image {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: var(--border-radius-small);
  flex-shrink: 0;
}

.cart-item__info {
  flex: 1;
  min-width: 0;
}

.cart-item__name {
  font-size: 1rem;
  font-weight: 600;
  color: var(--color-dark);
  margin-bottom: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cart-item__price {
  font-size: 0.95rem;
  color: var(--color-primary);
  font-weight: 600;
}

.cart-item__controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

.cart-item__control-btn {
  width: 32px;
  height: 32px;
  border: none;
  background: var(--color-accent);
  color: var(--color-light);
  border-radius: 50%;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s;
}

.cart-item__control-btn:hover {
  background: var(--color-accent-hover);
}

.cart-item__quantity {
  min-width: 24px;
  text-align: center;
  font-weight: 600;
  color: var(--color-dark);
}`, "",{"version":3,"sources":["webpack://./src/components/CartItem/CartItem.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,aAAa;EACb,oCAAoC;EACpC,0CAA0C;EAC1C,0BAA0B;EAC1B,qCAAqC;AACvC;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,yCAAyC;EACzC,cAAc;AAChB;;AAEA;EACE,OAAO;EACP,YAAY;AACd;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,wBAAwB;EACxB,kBAAkB;EAClB,mBAAmB;EACnB,gBAAgB;EAChB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,2BAA2B;EAC3B,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,YAAY;EACZ,+BAA+B;EAC/B,yBAAyB;EACzB,kBAAkB;EAClB,iBAAiB;EACjB,iBAAiB;EACjB,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iCAAiC;AACnC;;AAEA;EACE,qCAAqC;AACvC;;AAEA;EACE,eAAe;EACf,kBAAkB;EAClB,gBAAgB;EAChB,wBAAwB;AAC1B","sourcesContent":[".cart-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 16px;\r\n  padding: 16px;\r\n  background: rgba(255, 255, 255, 0.7);\r\n  border-radius: var(--border-radius-medium);\r\n  backdrop-filter: blur(4px);\r\n  border: 1px solid rgba(0, 0, 0, 0.05);\r\n}\r\n\r\n.cart-item__image {\r\n  width: 60px;\r\n  height: 60px;\r\n  object-fit: cover;\r\n  border-radius: var(--border-radius-small);\r\n  flex-shrink: 0;\r\n}\r\n\r\n.cart-item__info {\r\n  flex: 1;\r\n  min-width: 0;\r\n}\r\n\r\n.cart-item__name {\r\n  font-size: 1rem;\r\n  font-weight: 600;\r\n  color: var(--color-dark);\r\n  margin-bottom: 4px;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.cart-item__price {\r\n  font-size: 0.95rem;\r\n  color: var(--color-primary);\r\n  font-weight: 600;\r\n}\r\n\r\n.cart-item__controls {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n}\r\n\r\n.cart-item__control-btn {\r\n  width: 32px;\r\n  height: 32px;\r\n  border: none;\r\n  background: var(--color-accent);\r\n  color: var(--color-light);\r\n  border-radius: 50%;\r\n  font-size: 1.1rem;\r\n  font-weight: bold;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify