'use strict'

export interface APIresponse {
	service22: {
		success: string
		error: string
	}
	body: any
}

export class API {
	public static readonly BASE_URL = 'http://localhost:8080/api/v0'

	static async fetch(
		inputRelative: string,
		init?: RequestInit,
	): Promise<Response> {
		if (!init) {
			init = {}
		}

		if (!init.headers) {
			init.headers = { 'Content-Type': 'application/json' }
		}

		init.credentials = 'include'

		const response = await fetch(API.BASE_URL + inputRelative, init)

		return response
	}
}
